<template>
  <el-dialog title="上传图片">
    <el-row>
      <el-col :span="12">
        <el-upload
          class="avatar-uploader up-box"
          action="/upload"
          drag
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
        >
          <img v-if="imageUrl" :src="imageUrl" class="avatar" />
          <el-icon v-else class="avatar-uploader-icon"><Plus /></el-icon>
        </el-upload>
      </el-col>
      <el-col :span="12">
        <el-table class="up-box" :data="fileprops" border>
          <el-table-column prop="name" label="属性"></el-table-column>
          <el-table-column prop="value" label="值"></el-table-column>
        </el-table>
      </el-col>
    </el-row>

    <div>
      <el-button type="success" @click="save"> 保存 </el-button>
      <el-button type="default" @click="cancel"> 取消 </el-button>
    </div>
  </el-dialog>
</template>

<script setup>
import { Plus } from "@element-plus/icons-vue";
</script>

<script>
import { ElMessage } from "element-plus";

function nop() {}

export default {
  name: "update-up-dialog",

  data() {
    return {
      imageUrl: "",
      vo: {},
      item: {},
      fileprops: [{ name: "", value: "" }],
    };
  },

  methods: {
    handleAvatarSuccess(response, uploadFile) {
      nop(response);
      let vo = response;
      let item = vo.mediae[0];

      this.vo = vo;
      this.item = item;
      this.fileprops = this.makeFileProps(item);
      this.imageUrl = URL.createObjectURL(uploadFile.raw);
    },

    makeFileProps(item) {
      const key_and_name = {
        name: "文件名",
        size: "大小",
        content_type: "类型",
        sha256sum: "SHA-256",
      };
      let dst = [];
      for (var key in key_and_name) {
        let name = key_and_name[key];
        let value = item[key];
        dst.push({ name, value });
      }
      return dst;
    },

    beforeAvatarUpload(rawFile) {
      if (!this.acceptType(rawFile.type)) {
        ElMessage.error("不支持的文件格式!");
        console.warn("不支持的文件格式: " + rawFile.type);
        return false;
      } else if (rawFile.size / 1024 / 1024 > 2) {
        ElMessage.error("文件太大了，最多支持 2MB!");
        return false;
      }
      return true;
    },

    acceptType(type) {
      const list = [
        "image/jpeg",
        "image/png",
        "image/gif",
        "image/svg",
        "image/webp",
        "image/svg+xml",
      ];
      for (var i in list) {
        let want = list[i];
        if (want == type) {
          return true;
        }
      }
      return false;
    },

    save() {
      let item = this.item;
      this.$store.dispatch("media/insert", { item }).then(() => {
        this.close();
      });
    },

    cancel() {
      this.close();
    },

    close() {
      this.$emit("update:modelValue", false);
    },
  },
};
</script>

<style scoped>
.avatar-uploader .avatar {
  width: 178px;
  height: 178px;
  display: block;
}

.up-box {
  margin: 10px;
}
</style>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed var(--el-border-color);
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: var(--el-transition-duration-fast);
}

.avatar-uploader .el-upload:hover {
  border-color: var(--el-color-primary);
}

.el-icon.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  text-align: center;
}
</style>
