<template>
  <div>
    <el-table :data="items" @selection-change="handleSelectionChange">
      <el-table-column type="selection"></el-table-column>

      <el-table-column prop="url" label="" width="90">
        <template #default="scope">
          <img class="media-item-image" :src="scope.row.url" />
        </template>
      </el-table-column>

      <el-table-column prop="label" label="名称" sortable></el-table-column>

      <el-table-column
        prop="content_type"
        label="类型"
        sortable
      ></el-table-column>

      <el-table-column prop="size" label="大小" sortable></el-table-column>

      <!-- <el-table-column prop="path" label="路径"></el-table-column> -->

      <el-table-column label="操作" width="170px">
        <template #default="scope">
          <el-button
            link
            type="primary"
            @click="handleClickItemDetail(scope.row)"
            >详情</el-button
          >

          <el-button
            link
            type="danger"
            @click="handleClickRemoveItem(scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <MyBatch v-model="selectedItems" />

    <MyItemDetailDialog
      v-model="displayDetailDialog"
      :target="theDetailDialogItem"
    />
  </div>
</template>

<script>
import { ElMessageBox } from "element-plus";
import MyBatch from "./batch.vue";
import MyItemDetailDialog from "./item-detail-dialog.vue";

export default {
  name: "settings-mediae-table",

  components: { MyBatch, MyItemDetailDialog },

  computed: {
    items() {
      return this.$store.getters["media/list"];
    },
  },

  data() {
    return {
      selectedItems: [],
      theDetailDialogTarget: {},
      displayDetailDialog: false,
    };
  },

  methods: {
    handleClickItemDetail(/* item */) {},

    handleClickRemoveItem(item) {
      let title = "移除媒体文件";
      let text = "确定要移除 [" + item.name + "] 这个媒体文件吗？";
      let desc = "注意：移除该文件可能会导致引用它的内容显示不正常！";
      ElMessageBox.confirm(text + desc, title, {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.removeItem(item);
        })
        .catch(() => {});
    },

    handleClickItemDetail(item) {
      this.theDetailDialogItem = item;
      this.displayDetailDialog = true;
    },

    removeItem(item) {
      this.$store.dispatch("media/remove", { item });
    },

    handleSelectionChange(items) {
      this.selectedItems = items;
    },
  },
};
</script>

<style scoped>
.media-item-image {
  max-width: 64px;
  max-height: 64px;
  width: auto;
}
</style>
