<template>
  <div class="toolbar">
    <el-button type="default" @click="refresh">
      <el-icon> <Refresh /> </el-icon> 刷新
    </el-button>

    <el-button type="default" @click="insert">
      <el-icon> <Plus /> </el-icon> 添加
    </el-button>

    <el-button type="default" @click="importPresets">
      <el-icon> <Plus /> <Plus /> </el-icon> 导入预置资源
    </el-button>
  </div>
</template>

<script setup>
import { Refresh, Plus } from "@element-plus/icons-vue";
</script>

<script>
export default {
  name: "settings-mediae-toolbar",

  methods: {
    fire(name) {
      let e = { name };
      this.$emit("action", e);
    },

    insert() {
      this.fire("insert");
    },

    refresh() {
      this.fire("refresh");
    },

    importPresets() {
      this.fire("import-presets");
    },
  },
};
</script>

<style scoped>
.toolbar {
  text-align: right;
}
</style>
