<template>
  <!-- 批处理工具栏 -->
  <div class="utils-batch-tool-bar" v-show="!hide">
    <span> {{ title }} </span>
    <span class="the-count-box"> {{ count }} 项 </span>
    <slot> </slot>
  </div>
</template>
  
<script>
export default {
  name: "utils-batch-tool-bar",

  computed: {
    items() {
      let list = this.modelValue;
      if (list == null) {
        list = [];
      }
      return list;
    },

    count() {
      let cnt = this.items.length;
      if (cnt == null) {
        return 0;
      }
      return cnt;
    },

    hide() {
      if (this.hideEmpty) {
        return this.count <= 0;
      }
      return false;
    },
  },

  props: {
    modelValue: Array,
    title: String,
    hideEmpty: Boolean,
  },
};
</script>
  
<style scoped>
.utils-batch-tool-bar {
  padding: 8px;
  background-color: rgb(240, 242, 245);
  color: rgb(99, 99, 99);
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  justify-items: center;
  align-items: center;
}

.the-count-box {
  margin-left: 5px;
  margin-right: 15px;
}
</style>
