<template>
    <div class="icon-parent"> <img class="icon-image" :src="src" :title="title" :style="imageStyle" />
    </div>
</template>

<script>
export default {
    computed: {
        imageStyle() {
            let size = this.size + "px";
            return {
                maxWidth: size,
                maxHeight: size,
            }
        }
    },

    props: {
        src: String,
        size: Number,
        title: String,
    }
}
</script>

<style scoped >
.icon-parent {
    padding: 0px;
    margin: 0px;
    display: inline-block;
}

.icon-image {
    padding: 0px;
    margin: 0px;
}
</style>
