<template>
  <div>
    <el-pagination
      v-model:current-page="currentPage"
      v-model:page-size="pageSize"
      :page-sizes="[5, 10, 20, 50, 100, 200, 500]"
      :background="background"
      layout="total, sizes, prev, pager, next"
      :total="theCountItemsTotal"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    />
  </div>
</template>

<script>
export default {
  name: "settings-mediae-pager",

  computed: {
    theCountItemsTotal() {
      return this.$store.getters["media/total"];
    },
  },

  data() {
    return {
      currentPage: 1,
      pageSize: 10,
      background: true,
    };
  },

  methods: {
    handleSizeChange(value) {
      let size = value;
      let index = 0;
      this.$store.commit("media/update", { index, size });
    },

    handleCurrentChange(value) {
      let index = value - 1;
      this.$store.commit("media/update", { index });
    },
  },
};
</script>
