<template>
  <div class="settings-mediae-index-page">
    <h1>媒体文件</h1>

    <MyToolbar @action="handleAction" />
    <MyPager class="settings-mediae-index-pager" />
    <MyTable />

    <MyUpImageDialog v-model="displayUploadDialog" />
  </div>
</template>

<script>
import MyToolbar from "./toolbar.vue";
import MyPager from "./pager.vue";
import MyTable from "./table.vue";
import MyUpImageDialog from "@/components/upload/up-image-dialog.vue";

export default {
  name: "settings-mediae-index",

  components: { MyToolbar, MyTable, MyUpImageDialog, MyPager },

  data() {
    return {
      displayUploadDialog: false,
    };
  },

  methods: {
    fetch() {
      this.$store.dispatch("media/fetch", { all: 1 });
    },

    upload() {
      this.displayUploadDialog = true;
    },

    importPresets() {
      this.$store.dispatch("media/import_presets", {});
    },

    handleAction(e) {
      let name = e.name;
      if (name == "refresh") {
        this.fetch();
      } else if (name == "import-presets") {
        this.importPresets();
      } else if (name == "insert") {
        this.upload();
      }
    },
  },

  mounted() {
    this.fetch();
  },
};
</script>

<style scoped>
.settings-mediae-index-page {
  text-align: left;
}

.settings-mediae-index-pager {
  margin-top: 20px;
  margin-bottom: 20px;
}
</style>
